	module.exports = {"data":{"wordpressPost":{"title":"Most read articles in December","content":"<p>The most read article in December was Bellis and colleagues&#8217; study of mortality amongst rock and pop stars, which also received a great deal of press attention. The second most read was Rajavashisth <em>et al</em>&#8216;s study of diabetes prevalence amongst marijuana users which was originally published way back in February 2012. Bergman <em>et al</em>&#8216;s newly published paper on Vitamin D3 supplementation and frequent respiratory tract infections was also highly read.</p>\n<p>&nbsp;</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Bellis <em>et al</em></td>\n<td valign=\"top\" width=\"301\">\n<p id=\"article-title-1\"><a href=\"http://bmjopen.bmj.com/content/2/6/e002089.full\">Dying to be famous: retrospective cohort study of rock and pop star mortality and its association with adverse childhood experiences</a></p>\n</td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Rajavashisth <em>et al</em></td>\n<td valign=\"top\" width=\"301\">\n<p id=\"article-title-1\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000494.full\">Decreased prevalence of diabetes in marijuana users: cross-sectional data from the National Health and Nutrition Examination Survey (NHANES) III</a></p>\n</td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Bergman <em>et al</em></td>\n<td valign=\"top\" width=\"301\">\n<p id=\"article-title-1\"><a href=\"http://bmjopen.bmj.com/content/2/6/e001663.full\">Vitamin D<sub>3</sub> supplementation in patients with frequent respiratory tract infections: a randomised and double-blind intervention study</a></p>\n</td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Kern <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/5/e001288.long\">Does low-dose acetylsalicylic acid prevent cognitive decline in women with high cardiovascular risk? A 5-year follow-up of a non-demented population-based cohort of Swedish elderly women</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Kripke <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/1/e000850.full\">Hypnotics’ association with mortality or cancer: a matched cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Tikkinen <em>et al</em></td>\n<td valign=\"top\" width=\"301\">\n<p id=\"article-title-1\"><a href=\"http://bmjopen.bmj.com/content/2/6/e001632.full\">What is a disease? Perspectives of the public, health professionals and legislators</a></p>\n</td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Arroll and Howard</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/6/e001079.long\" target=\"_blank\">A preliminary prospective study of<br />\nnutritional, psychological and combined<br />\ntherapies for myalgic encephalomyelitis/<br />\nchronic fatigue syndrome (ME/CFS) in a<br />\nprivate care setting</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Swinglehurst <em>et al</em></td>\n<td valign=\"top\" width=\"301\">\n<p id=\"article-title-1\"><a href=\"http://bmjopen.bmj.com/content/2/6/e001754.full\">Computer templates in chronic disease management: ethnographic case study in general practice</a></p>\n</td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Lee <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/1/2/e000269.long\">UK stroke incidence, mortality and cardiovascular risk management 1999–2008: time-trend analysis from the General Practice Research Database</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Teder <em>et al</em></td>\n<td valign=\"top\" width=\"301\">\n<p id=\"article-title-1\"><a href=\"http://bmjopen.bmj.com/content/2/2/e000268.full\">Family-based behavioural intervention programme for obese children: a feasibility study</a></p>\n</td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"0fbd1641-1340-56f8-8234-6eda3dc770b5"}}

